<template>
  <div id="app">
    <duo-3d-sphere
      :table="table"
      :starSize="starSize"
      :starTail="starTail"
      :starAlpha="starAlpha"
      :starAmount="starAmount"
      :sphereSkin="sphereSkin"
      :spherePositionX="spherePositionX"
      :spherePositionY="spherePositionY"
      :spherePositionZ="spherePositionZ"
      :starSpeedReciprocal="starSpeedReciprocal"
      :starsDiameterReciprocal="starsDiameterReciprocal"
      :starIsClockwiseRotation="starIsClockwiseRotation"
    />
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      table: [],
      starSize: 4,
      starTail: 0.5,
      starAlpha: 20,
      starAmount: 200,
      spherePositionX: 50,
      spherePositionY: 50,
      sphereSkin: "shallowBlue",
      spherePositionZ: 2500,
      starSpeedReciprocal: 20,
      starsDiameterReciprocal: 2,
      starIsClockwiseRotation: true,
    };
  },
  mounted() {
    this.generateData();
  },
  methods: {
    generateData() {
      let data = [],
        firstNames = "赵钱孙李周吴郑王",
        lastNames = "云鹤九霄龙腾四海";

      for (let i = 0; i < 100; i++) {
        let f = parseInt(Math.random() * firstNames.length),
          l = parseInt(Math.random() * lastNames.length),
          name = firstNames[f] + lastNames[l];
        data.push({
          title: `${name}`,
          details: "10",
        });
      }
      this.table = data;
    },
  },
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
</style>



